.header_outer_container{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: red;
    height: 10vh;
    transition: all 0.5s;
    transition-timing-function: ease;
    background-color: rgb(20,20,20);
    background-color: transparent;    
    z-index: 1;
}
.nav_black{
    background-color: rgb(20,20,20);
}
.header_container{
    display: flex;
    width: 90%;
    justify-content: space-between;
    align-items: center;



}
.header_right ul{
    width: 150%;
    display: flex;
    justify-content: space-around;
    list-style: none;
    cursor: pointer;

    
}
.header_left ul{
    width: 140%;
    display: flex;
    justify-content: space-around;
    list-style: none;
    cursor: pointer;
    align-items: center;

}